.green-dot {
  position: relative;
  background-color: #23a854;
  width: 15px;
  height: 15px;
  border-radius: 50%;
}

.green-dot::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  background-color: #23a854;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  animation: ping 1.5s infinite ease-out;
  opacity: 0;
}

@keyframes ping {
  0% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
  75% {
    transform: translate(-50%, -50%) scale(2);
    opacity: 0;
  }
  100% {
    transform: translate(-50%, -50%) scale(2);
    opacity: 0;
  }
}
